.centered {
    display: flex;
    display: -ms-flex;
    display: -moz-flex;
    display: -webkit-flex;
    align-items: center;
    justify-content: center;
    -webkit-align-items: center;
    -webkit-justify-content: center;
}

.sticker-grid-small {
    display: -moz-grid;
    display: -ms-grid;
    min-width: 64px;
    max-width: 444px; /* 296 */
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(64px, 1fr));
    grid-gap: 10px;
}

.sticker-grid-big {
    display: -moz-grid;
    display: -ms-grid;
    min-width: 96px;
    max-width: 320px;
    display: grid;
    grid-template-columns: repeat(auto-fill, 96px);
    grid-gap: 10px;
}

.card-elements-container {
    border-radius: 16px;
    -webkit-box-shadow: 0 0 8px 8px rgba(0, 0, 0, 0.022);
    box-shadow: 0 0 8px 8px rgba(0, 0, 0, 0.022);
    width: 100%;
}

.card-elements-container .tappable_top {
    border-top-left-radius: 16px;
    border-top-right-radius: 16px;
}

.card-elements-container .tappable_bottom {
    border-bottom-left-radius: 16px;
    border-bottom-right-radius: 16px;
}

.card-elements-container .cell {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
    flex-direction: row;
    -ms-flex-wrap: nowrap;
    flex-wrap: nowrap;
    -ms-flex-pack: justify;
    justify-content: space-between;
    padding: 14px 14px;
}

.card-elements-container .left {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
    -ms-flex-pack: center;
    justify-content: center;
}

.Link {
    color: inherit;
}
.CellButton {
    color: inherit;
}

.card-elements-container .text {
    font-size: 16px;
    font-weight: 400;
    line-height: 1.29;
    letter-spacing: -0.4px;
    margin-left: 8px;
}

.card-elements-container .right {
    align-self: center;
}